import React from 'react';
import { List, Avatar, Button, message } from 'antd';
import { UserOutlined, NumberOutlined, PhoneOutlined, LockOutlined, SafetyCertificateOutlined } from '@ant-design/icons';
import { changeUserStatusByUserId } from '../services/userService';

export class UserList extends React.Component {

  changeUserStatus = (userId) => {
    const data = { userId: userId };
    const callback = (data) => {
      message.success(data.msg);
      this.props.changeUserStatus(userId);
    };
    changeUserStatusByUserId(data, callback);
  }

  render () {
    return (
        <List
          itemLayout="horizontal"
          dataSource={this.props.userList}
          renderItem={(item) => (
            <List.Item>
              <List.Item.Meta
                avatar={<Avatar src={[require('../assets/avatar.jpg')]} />}
                title={(
                  <div>
                    <UserOutlined />
                    {' '}
                                  :
                    {' '}
                    {item.username}
                  </div>
                )}
                description={(
                  <div className="userListDescription">
                    <div className="userId">
                      <NumberOutlined />
                      {' '}
                                      ID:{item.userId}
                    </div>
                    <div className="nickname">
                      <UserOutlined />
                      {' '}
                                      :{item.nickname}
                    </div>
                    <div className="phone">
                      <PhoneOutlined />
                      {' '}
                                      :{item.phone}
                    </div>
                    <div className="password">
                      <LockOutlined />
                      {' '}
                                      :{item.password}
                    </div>
                    <div className="userType">
                      <SafetyCertificateOutlined />
                                      :
                      {' '}
                      {
                        (() => {
                          switch (item.userType) {
                            case -1: return (<span></span>);
                            case 0: return (<span></span>);
                            case 1: return (<span></span>);
                            default: break;
                          }
                        })()
                      }
                    </div>
                  </div>
                )}
              />
              <div className="userListButton" style={{ float: 'right' }}>
                {
                  item.userType < 0
                    ? (
                      <Button type = "primary"onClick={this.changeUserStatus.bind(this, item.userId)}></Button>
                    )
                    : (
                      item.userType === 0
                        ? (
                          <div />
                        )
                        : (
                          <Button onClick={this.changeUserStatus.bind(this, item.userId)}></Button>
                        )
                    )
                }
              </div>
            </List.Item>
          )}
        />
    );
  }
}
